@import url(https://fonts.googleapis.com/css2?family=Arvo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Saira+Extra+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body {
  background-color: rgb(223, 192, 192) !important;
}

.logo {
  position: absolute;
  width: 120px;
  height: 120px;
  left: 15px;
  top: 15px;
}

nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  min-height: 135px;
}

nav .header {
  height: 135px;
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  right: 0px;
  top: 0px;
  width: 50%;
  -webkit-flex-direction: row;
          flex-direction: row;
}

a {
  font-family: "Saira Extra Condensed", sans-serif;
  font-size: 30px;
  text-decoration: none;
  color: rgb(37, 37, 37);
}

a:hover {
  color: rgb(78, 78, 78);
  border-bottom: 5px solid rgb(78, 78, 78);
}

a:active {
  color: rgb(128, 128, 128);
  border-bottom: 5px solid rgb(128, 128, 128);
}

a:focus {
  color: rgb(255, 255, 255);
  border-bottom: 5px solid rgb(255, 255, 255);
}

h1 {
  font-family: "Fredericka the Great", cursive;
  font-size: 40px;
  color: rgb(37, 37, 37);
  text-align: center;
  margin-top: 40px;
  margin-bottom: 40px;
}

.burger {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  right: 14%;
  top: 20px;
  cursor: pointer;
  opacity: 0;
}

.burger div {
  position: relative;
  right: 0px;
  width: 25px;
  height: 2px;
  background-color: rgb(37, 37, 37);
  margin: 5px;
  transition: all 0.3s ease;
}

/*This gets rid of the menu background when the screen is bigger*/
.background {
  opacity: 0;
}

/*If the screen is smaller than 600 px it switches to small mode*/
@media screen and (max-width: 600px) {
  body {
    overflow-x: hidden;
    background-size: 330%, 100%;
    background-position: top, right;
  }

  .background {
    z-index: 5;
    opacity: 0;
    position: absolute;
    display: flexbox;
    background-color: rgba(29, 29, 29, 0.671);
    right: 0px;
    top: 0px;
    bottom: 0px;
    width: 35%;
  }

  .homeButton {
    z-index: 200;
    position: absolute;
    right: 10px;
    text-align: center;
    font-size: 30px;
    color: white;
    height: 47px;
    top: 15vh;
    width: 60%;
    z-index: 200;
    opacity: 0;
  }

  .browseButton {
    position: absolute;
    text-align: center;
    right: 10px;
    font-size: 30px;
    height: 47px;
    color: white;
    top: 28vh;
    width: 60%;
    z-index: 200;
    opacity: 0;
  }

  .burger {
    z-index: 6;
    display: block;
    opacity: 100;
  }
  a:hover {
    color: rgb(255, 255, 255);
    border-bottom: 5px solid rgb(255, 255, 255);
  }

  a:active {
    color: rgb(255, 255, 255);
    border-bottom: 5px solid rgb(255, 255, 255);
  }

  a:focus {
    border-bottom: 5px solid rgb(255, 255, 255);
  }
}

/*If the burger is pressed this makes the menu appear*/
.nav1-active {
  opacity: 100;
}

/*This is the animation function for text menu elements*/
@-webkit-keyframes navLinkFade {
  from {
    opacity: 0;
    -webkit-transform: translate(100px);
            transform: translate(100px);
  }
  to {
    opacity: 100;
    -webkit-transform: translate(0px);
            transform: translate(0px);
  }
}
@keyframes navLinkFade {
  from {
    opacity: 0;
    -webkit-transform: translate(100px);
            transform: translate(100px);
  }
  to {
    opacity: 100;
    -webkit-transform: translate(0px);
            transform: translate(0px);
  }
}

/*These toggle functions are what animate the burger.*/
.toggle .line1 {
  -webkit-transform: rotate(-45deg) translate(-5px, 4.5px);
          transform: rotate(-45deg) translate(-5px, 4.5px);
  background-color: white;
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  -webkit-transform: rotate(45deg) translate(-5px, -5px);
          transform: rotate(45deg) translate(-5px, -5px);
  background-color: white;
}

.shadow { /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */ /* Firefox 3.5 - 3.6 */
  box-shadow: 3px 3px 5px 6px #ccc; /* Opera 10.5, IE 9, Firefox 4+, Chrome 6+, iOS 5 */
}

